.navbar-brand h1 {
  font-family: "Sansita Swashed", cursive;
  font-size: 4.5rem;
}

.icon {
  font-size: 100px;
  display: inline-block;
  transform: scale(1.2);
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  z-index: 1;
  justify-content: space-between;
  width: 100%;
  height: 138px;
  background-color: #fff;
  color: #333;
  transform: translateY(0);
  transition: transform 0.6s ease;
  
}


.header.shadow {
  box-shadow: 0 9px 9px -9px rgba(0, 0, 0, 0.13);
}

.header.hidden {
  transform: translateY(-110%);
}
@media screen and (max-width: 940px) {
  div.hideonmobile {
    display: none;
  }

  .navbar-brand h1 {
    font-size: 2.5rem;
  }

  .icon {
    font-size: 50px;
    padding-right: 20px;
    display: inline-block;
    transform: scale(0.8);
  }
}

